import "./Calculator.css"; 
import React from "react";

let numbers = ["zero", "one", "two", "three", "four", "five", "six", "seven", "eight", "nine"];
let operators = ["add", "subtract", "multiply", "divide", "decimal","+", "-", "*", "/", "."];
let numbers_map = {
    "zero": "0",
    "one": "1",
    "two": "2",
    "three": "3",
    "four": "4",
    "five": "5",
    "six": "6",
    "seven": "7",
    "eight": "8",
    "nine": "9"
}

let operators_map = {
    "add": "+",
    "subtract": "-",
    "multiply": "*",
    "divide": "/",
    "decimal": "."
}

export class Calculator extends React.Component {
    constructor(props) {
        super(props); 
        this.state = {
            formula: "", 
            result: "0"
        }
    }
    handleClear = () => {
        this.setState({
            formula: "", 
            result: "0"
        })
    }

    handleClick = (e) => {
        let key = e.target.id; // Key pressed 
        let actual_formula = this.state.formula;
        let actual_result = this.state.result;

        if (actual_formula[actual_formula.length - 1] == "=") { // If the last character of the formula is an equal sign
            if (operators.includes(key)) { // If an operator was pressed
                this.setState({
                    formula : actual_result,
                    result: "" 
                })
                actual_formula = actual_result;
                actual_result = ""; 
            } else { // If a number was pressed, reset the calculator
                actual_formula = ""; 
                actual_result = "0"; 
            }
            
        }

        if (numbers.includes(key)) { // If a number was pressed
            if (operators.includes(actual_result)) { // If the actual display is an operator
                this.setState({
                    formula: actual_formula + numbers_map[key],
                    result: numbers_map[key]
                })
            } else { // If the actual display is a number
                if (actual_result == "0") { // If the actual display is 0
                    if (key != "zero") { // If the number is different than 0
                        this.setState({
                            formula: actual_formula + numbers_map[key],
                            result:  numbers_map[key]
                        })
                    } 
                }
                else { // If the actual display is not 0
                    this.setState({
                        formula: actual_formula + numbers_map[key],
                        result: actual_result + numbers_map[key]
                    })
                }
            }
        } else  {// If an operator was pressed
            if (operators.includes(actual_result)) { // If the actual display is an operator
                if (key == "subtract" && actual_result != "-") { // If the operator is a minus, add it to the formula
                    this.setState ({
                        formula: actual_formula + operators_map[key],
                        result: operators_map[key]
                    })
                } else if (actual_result != operators_map[key]) { // If the actual display is not the same operator, replace it with the new one
                    if (actual_formula.slice(-2).split("").every((char) => operators.includes(char))) { // If there was an operator before the actual one including a minus, replace it with the new one
                        let new_formula = actual_formula.slice(0, -2) + operators_map[key]; 
                        this.setState ({ 
                            formula: new_formula,
                            result: operators_map[key] 
                        });
                    } else { // If there was just one operator before the actual one, replace it with the new one
                        let new_formula = actual_formula.slice(0, -1) + operators_map[key];
                        this.setState ({ 
                            formula: new_formula,
                            result: operators_map[key] 
                        })
                    }
                }
            } else { // If the actual display is a number
                this.setState({
                    formula: actual_formula + operators_map[key],
                    result: operators_map[key]
                })
            }
        }
    }

    handleDecimal = () => {
        let actual_formula = this.state.formula; 
        let actual_result = this.state.result;
        let testRegex = /\./; 
        if (testRegex.test(actual_result)) { // If the result  is already decimal 
            console.log("Already decimal"); 
        } else { // I the result is not decimal
            if (operators.includes(actual_result)) { // If the actual display is an operator
                this.setState({
                    formula: actual_formula + "0.",
                    result: "0."
                })
            } else { // If the actual display is a number
                this.setState({
                    formula: actual_formula + ".",
                    result: actual_result + "."
                })
            }
        }
    }

    handleEqual = () => {
        if (this.state.formula[this.state.formula.length - 1] == "=") { // If the last character of the formula is an equal sign
            return; 
        }
        let actual_formula = this.state.formula;
        let operation_result = eval(actual_formula);
        this.setState({ 
            formula: actual_formula + "=",
            result: operation_result

        })
    }

    detectKey = (e) => {
        switch(e.key) {
            case "0":
                document.getElementById("zero").click();
                break;
            case "1":
                document.getElementById("one").click();
                break;
            case "2":
                document.getElementById("two").click();
                break;
            case "3":
                document.getElementById("three").click();
                break;
            case "4":
                document.getElementById("four").click();
                break;
            case "5":
                document.getElementById("five").click();
                break;
            case "6":
                document.getElementById("six").click();
                break;
            case "7":
                document.getElementById("seven").click();
                break;
            case "8":
                document.getElementById("eight").click();
                break;
            case "9":
                document.getElementById("nine").click();
                break;
            case "+":
                document.getElementById("add").click();
                break;
            case "-":
                document.getElementById("subtract").click();
                break;
            case "*":
                document.getElementById("multiply").click();
                break;
            case "/":
                document.getElementById("divide").click();
                break;
            case ".":
                document.getElementById("decimal").click();
                break;
            case "Enter":
                document.getElementById("equals").click();
                break;
            case "Backspace":   
                document.getElementById("clear").click();
                break;
            default: 
                break; 
        }
    }
    componentDidMount(){
        document.addEventListener("keydown", this.detectKey, false);
    }
    componentWillUnmount(){
        document.removeEventListener("keydown", this.detectKey, false);
    }

    render () {
        return( 
            <div id="calculator">
                <div id="calculator-display">
                    <div id="formula-screen" className="display_props">
                        {this.state.formula}
                    </div>
                    <div id="display" className="display_props">
                        {this.state.result}
                    </div>
                    
                </div>
                <button id="clear" className="red-button" onClick={this.handleClear}>AC</button>
                <button id="decimal" className="dark-grey-button" onClick={this.handleDecimal}>.</button>
                <button id="zero" className="dark-grey-button" onClick={this.handleClick}>0</button>
                <button id="one" className="dark-grey-button" onClick={this.handleClick}>1</button>
                <button id="two" className="dark-grey-button" onClick={this.handleClick}>2</button>
                <button id="three" className="dark-grey-button" onClick={this.handleClick}>3</button>
                <button id="four" className="dark-grey-button" onClick={this.handleClick}>4</button>
                <button id="five" className="dark-grey-button" onClick={this.handleClick}>5</button>
                <button id="six" className="dark-grey-button" onClick={this.handleClick}>6</button>
                <button id="seven" className="dark-grey-button" onClick={this.handleClick}>7</button>
                <button id="eight" className="dark-grey-button" onClick={this.handleClick}>8</button>
                <button id="nine" className="dark-grey-button" onClick={this.handleClick}>9</button>
                <button id="add" className="light-grey-button" onClick={this.handleClick}>+</button>
                <button id="subtract" className="light-grey-button" onClick={this.handleClick}>-</button>
                <button id="multiply" className="light-grey-button" onClick={this.handleClick}>x</button>
                <button id="divide" className="light-grey-button" onClick={this.handleClick}>/</button>
                <button id="equals" className="blue-button" onClick={this.handleEqual}>=</button>
            </div>

        ); 
    }
}